.image-section {
  width: 100%;
  margin-top: 80px;
}
.gradient-image {
  margin: 3.5rem 0px;
  display: flex;
  justify-content: center;
  border-radius: 20px;
  position: relative;
  object-fit: cover;
}
.gradient-background {
  width: 100%;
  max-width: 100%;
  height: auto;
  border-radius: 15px;
  transition: all 0.7s ease;
  opacity: 0;
}
.opacity-full {
  opacity: 1;
}

.image-style {
  position: absolute;
  width: 90%;
  height: 98%;
  border-radius: 20px;
  top: 10%;
  right: 50%;
  border-radius: 15px;
  transition: all 0.7s ease;
  opacity: 0;
  transform: translate(50%, 50px);
}
.image-visible {
  opacity: 1;
  transform: translate(50%, 0px);
}
@media all and (min-width: 576px) {
  .image-section {
    width: 100%;
    margin-top: 20px;
  }
  .image-section {
    padding: 20px;
  }
  .gradient-background {
    border-radius: 20px;
  }
  .image-style {
    border-radius: 20px;
  }
}
@media all and (min-width: 768px) {
  .image-section {
    padding: 45px;
  }
}
@media all and (min-width: 992px) {
  .shape-1 {
    right: -15%;
    top: -10%;
  }
  .shape-2 {
    top: -10%;
    left: -17%;
  }
  .shape-3 {
    bottom: -32% !important;
    left: -16%;
  }
}

@keyframes bgAnimation {
  100% {
    opacity: 1;
  }
}
