.email-form {
  width: min(100%, 600px);
  display: flex;
  justify-content: center;
  margin: 15px auto 0px;
  padding: 10px 10px;
  border-radius: 50px;
  background-color: var(--primeBgColor);
  opacity: 1 !important;
  transition: all 0.3s;
  cursor: pointer;
}
.email-form:hover {
  background-color: #4d4d63;
}
.submit-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.email-form input {
  font-size: calc(0.8rem + 0.5vw);
  padding: 5px 10px;
  direction: ltr;
  background-color: transparent;
  border: none;
  width: 60%;
  outline: none;
  color: #fff;
  font-family: "Noto Sans Arabic";
  opacity: 1 !important;
  height: 70%;
}
.button-style {
  width: 40%;
  padding: 4px 0px;
  text-align: center;
  font-size: calc(0.8rem + 0.5vw);
}
@media all and (min-width: 576px) {
  .email-form {
    margin: 18px auto 0px;
  }
  .submit-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
  }
  .email-form input {
    font-size: calc(0.9rem + 0.3vw);
    width: 70%;
    color: #fff;
    font-family: "Noto Sans Arabic";
    opacity: 1 !important;
  }
  .email-form input::placeholder {
    opacity: 0.75;
  }
  .button-style {
    width: 30%;
    padding: 5px 0px;
    font-size: calc(0.9rem + 0.3vw);
    background-color: red;
    position: static !important;
  }
}

@media all and (min-width: 768px) {
  .email-form {
    margin: 45px auto 0px;
  }
}
