.relative-wraper {
  position: relative;
  height: auto;
  margin: 2rem 0px;
  padding: 7rem 0px 7rem !important;
  text-align: center;
}

.relative-wraper > div.container {
  position: relative;
}
.shadow-box-brand {
  width: 160px;
  height: 100%;
  position: absolute;
  top: 0px;
  z-index: 5;
}
.dark-box1 {
  left: 0px !important;
  background-image: linear-gradient(90deg, #15151e 3%, rgba(21, 21, 30, 0) 98%);
}

.dark-box2 {
  right: 0px;
  background-image: linear-gradient(
    270deg,
    #15151e 3%,
    rgba(21, 21, 30, 0) 98%
  );
}
.brand-container {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  transition: all 0.6s ease;
  opacity: 0;
  scroll-behavior: smooth;
  overflow: hidden;
}
.brands-heading {
  padding: 0.5rem 0px 2rem;
  font-size: calc(1.3rem + 0.8vw);
  color: var(--textColor);
}

@media all and (min-width: 576px) {
  .shadow-box-brand {
    width: 210px;
  }
}
@media all and (min-width: 768px) {
  .brand-container {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    overflow: hidden;
  }
  .brands-heading {
    font-size: 1.6rem;
  }
}

.bar-visible {
  opacity: 1;
}
